<template>
  <div>
    <!-- Navbar -->
    <AppNavbar title="Add New Character" :showBackButton="true" />

    <!-- Form Container -->
    <div class="form-container card">
      <div class="form-group">
        <label for="name">Name:</label>
        <input id="name" v-model="character.name" placeholder="Enter character name" />
      </div>

      <div class="form-group">
        <label for="path">Path:</label>
        <input id="path" v-model="character.path" placeholder="Enter image URL" />
      </div>

      <div class="form-group">
        <label for="htmlCode">HTML Code:</label>
        <textarea id="htmlCode" v-model="character.htmlCode" placeholder="Enter HTML code"></textarea>
      </div>

      <div class="form-group">
        <label for="tags">Tags:</label>
        <input id="tags" v-model="tagsInput" placeholder="Enter tags, separated by commas" />
      </div>

      <!-- Action Buttons -->
      <div class="action-buttons">
        <button class="btn btn-save" @click="addCharacter">Add</button>
      </div>

      <!-- Popup for Error Message (Now within the card) -->
      <div v-if="showErrorPopup" class="error-popup-overlay">
        <div class="error-popup-box">
          <p>{{ errorMessage }}</p>
          <button class="btn btn-close" @click="closeErrorPopup">Close</button>
        </div>
      </div>

      <!-- Updated Overlay with Juicy Animation -->
      <AppOverlay :visible="added" message="Added!" />
    </div>
  </div>
</template>

<script>
import { push, ref, set } from 'firebase/database';
import { db } from '../firebase';
import AppNavbar from './AppNavbar.vue';
import AppOverlay from './AppOverlay.vue';

export default {
  components: {
    AppNavbar,
    AppOverlay,
  },
  data() {
    return {
      character: {
        name: '',
        path: '',
        htmlCode: '',
        tags: [],
      },
      tagsInput: '',
      added: false,
      showErrorPopup: false, // Controls visibility of the error popup
      errorMessage: '', // Holds the error message to display
    };
  },
  methods: {
    async addCharacter() {
      if (!this.character.name || !this.character.path) {
        this.errorMessage = 'Name and Path are required.';
        this.showErrorPopup = true; // Show error popup if validation fails
        return;
      }

      this.character.tags = this.tagsInput.split(',').map(tag => tag.trim());

      try {
        const newCharacterRef = push(ref(db, 'characters'));
        await set(newCharacterRef, this.character);

        this.added = true;
        this.character.name = '';
        this.character.path = '';
        this.character.htmlCode = '';
        this.tagsInput = '';

        setTimeout(() => {
          this.added = false;
          this.$router.push({ name: 'CharacterList' });
        }, 2000);
      } catch (error) {
        console.error('Error adding character:', error);
        this.errorMessage = 'Failed to add character.';
        this.showErrorPopup = true;
      }
    },
    closeErrorPopup() {
      this.showErrorPopup = false;
    },
  },
};
</script>

